import moment from 'moment'
import pdfMake from 'pdfmake/build/pdfmake'
import pdfFonts from 'pdfmake/build/vfs_fonts'
import Scoped from '@/services/Scoped'

pdfMake.vfs = pdfFonts.pdfMake.vfs

export async function printRekamMedik (data) {
  const today = moment()
  const hasilLab = await Scoped.RekamMedik.getHasilLab(data.rekamMedik.id)
  const penKhusus = await Scoped.PenangananKhusus.getPenangananKhusus(data.rekamMedik.id)

  let hasilLabContent = []
  let penKhususContent = [
    {text: 'Penanganan Khusus', style: 'subheader'},
    {
      style: 'tableExample',
      table: {
        widths: [120, '*'],
        body: penKhusus.map(it => {
          return [it.nama_tipe, it.deskripsi]
        }),
      },
      layout: {
        paddingTop: function(i, node) { return 4; },
        paddingBottom: function(i, node) { return 4; },
        hLineWidth: function (i, node) { return 0.5 },
        vLineWidth: function (i, node) { return 0 }
      }
    }
  ]

  hasilLab.forEach(hl => {
    hasilLabContent.push({ text: hl.nama_tipe, style: 'secondSubheader' })
    hasilLabContent.push(
      {
        style: 'secondTable',
        table: {
          widths: [120, '*'],
          body: hl.struktur.map(it => {
            return [it.nama, it.value]
          }),
        },
        layout: {
          paddingTop: function(i, node) { return 4 },
          paddingBottom: function(i, node) { return 4 },
          hLineWidth: function (i, node) { return 0.5 },
          vLineWidth: function (i, node) { return 0 }
        }
      }
    )
  })

    let content = [
        {text: `Detail Kunjungan ${data.rekamMedik.norek}`, style: 'header'},

        { text: 'Data Pasien', style: 'subheader' },
        {
        style: 'tableExample',
        table: {
            widths: [120, '*'],
            body: [
            ['Nama', data.pasien.nama],
            ['Jenis', data.pasien.jh_nama],
            ['ras', data.pasien.ras_nama],
            ['Lahir', moment(data.pasien.lahir).format('YYYY-MM-DD')],
            ['Sex', data.pasien.jk == 1 ? 'Jantan' : 'Betina'],
            ],
        },
        layout: 'noBorders'
        },

        {text: 'Data Pemilik', style: 'subheader'},
        {
        style: 'tableExample',
        table: {
            widths: [120, '*'],
            body: [
            ['Nama', data.pemilik.nama],
            ['Alamat', data.pemilik.alamat],
            ['No. Telp', data.pemilik.no_telp]
            ],
        },
        layout: 'noBorders'
        },

        { text: 'Rekam Medik', style: 'subheader' },
        {
        style: 'tableExample',
        table: {
            widths: [120, '*'],
            body: [
            ['No. Rekam Medik', data.rekamMedik.norek],
            ['Tipe Rekam Medik', data.pasien.jh_kode],
            ['Tanggal dan Waktu', data.rekamMedik.tanggal],
            ['Berat', data.rekamMedik.berat + ' kg'],
            ['Freq. N', data.rekamMedik.freq_n],
            ['Freq. P', data.rekamMedik.freq_p],
            ['Freq. T', data.rekamMedik.freq_t]
            ],
        },
        layout: {
            paddingTop: function(i, node) { return 4; },
            paddingBottom: function(i, node) { return 4; },
            hLineWidth: function (i, node) { return 0 },
            vLineWidth: function (i, node) { return 0 }
        }
        },

        { text: 'Kondisi', style: 'subheader' },
        {
        style: 'tableExample',
        table: {
            widths: [150, '*'],
            body: [
            ['Anamnesis', data.rekamMedik.anamnesis],
            ['Keadaan Umum', data.rekamMedik.keadaan_umum],
            ['MTH', data.rekamMedik.mth],
            ['Mulut', data.rekamMedik.mulut],
            ['Kulit Rambut', data.rekamMedik.kul_rambut],
            ['Kelenjar Limfe', data.rekamMedik.kelenjar_limfe],
            ['Pernapasan', data.rekamMedik.pernapasan],
            ['Peredaran Darah', data.rekamMedik.peredaran_darah],
            ['Pencernaan', data.rekamMedik.pencernaan],
            ['Kelamin Perkencingan', data.rekamMedik.kelamin_perkencingan],
            ['Anggota Gerak', data.rekamMedik.ang_gerak],
            ['Diagnosa', data.rekamMedik.diagnosa],
            ['Prognosis', data.rekamMedik.prognosis],
            ['Terapi', data.rekamMedik.terapi]
            ],
        },
        layout: {
            paddingTop: function(i, node) { return 4 },
            paddingBottom: function(i, node) { return 4 },
            hLineWidth: function (i, node) { return 1 },
            vLineWidth: function (i, node) { return 0 }
        }
        }
    ]

    if (hasilLab.length > 0) {
        content.push({text: 'Hasil Lab', style: 'subheader'})
        hasilLabContent.forEach(c => {
            content.push(c)
        })
    }

    if (penKhusus.length > 0) {
        penKhususContent.forEach(c => {
            content.push(c)
        })
    }

  let styles = {
    header: {
      fontSize: 18,
      bold: true,
      margin: [0, 0, 0, 16]
    },
    subheader: {
      fontSize: 16,
      bold: true,
      margin: [0, 20, 0, 5]
    },
    secondSubheader: {
      fontSize: 14,
      bold: true,
      margin: [20, 30, 0, 5]
    },
    tableExample: {
      margin: [0, 5, 0, 15]
    },
    secondTable: {
      margin: [24, 5, 0, 15]
    },
    tableHeader: {
      bold: true,
      fontSize: 13,
      color: 'black'
    }
  }

  let pdfContent = pdfMake.createPdf({
      content,
      styles
  })

  pdfContent.print()
}



